<template>
  <div>
    <a-input-group compact>
      <!--  -->
      <a-select :defaultValue="type" @change="typeChangeAction">
        <a-select-option value="tel"></a-select-option>
        <a-select-option value="email"></a-select-option>
        <a-select-option value="user"></a-select-option>
      </a-select>
      <!--  -->
      <a-input style="width: 50%" :defaultValue="account" @change="accountChangeAction" />
    </a-input-group>
  </div>
</template>

<script>
export default {
  props: {
    value: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      type: this.value.type || "tel",
      account: this.value.account || ""
    };
  },
  watch: {
    value(newVal) {
      Object.assign(this, newVal);
    }
  },
  methods: {
    typeChangeAction(type) {
      this.$emit("change", { ...this.value, type });
    },
    accountChangeAction({ target: { value: account } }) {
      this.$emit("change", { ...this.value, account, type: this.type });
    }
  }
};
</script>

